import { cilEnvelopeLetter, cilPeople } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import {
  CButton,
  CTableDataCell,
  CTableHeaderCell,
  CTableRow,
} from "@coreui/react";
import React, { useContext } from "react";
import { Link } from "react-router-dom";
import UtilsContext from '../../../contexts/Utils/UtilsContext';

function ReadOnly({
  ele,
  // eslint-disable-next-line
  index,
  setDel,
  setConfirmDelname
}) {
  const utilsContext = useContext(UtilsContext);
  const { edit, setEdit } = utilsContext;
  return (
    <CTableRow>
      <CTableHeaderCell scope="row">{index + 1}</CTableHeaderCell>
      <CTableDataCell><CIcon icon={cilPeople} size="lg"></CIcon> {ele.firstname}{" "}{ele.lastname}</CTableDataCell>
      <CTableDataCell><CIcon icon={cilEnvelopeLetter} size="lg"></CIcon> {ele.email}</CTableDataCell>
      <CTableDataCell> {ele.userstatus == "active" ? "🟢 Active" : (ele.userstatus == "suspended" ? "🔴 Suspended" : (ele.userstatus == "deleted" ? "⚫ Deleted" :"🟡 Pending"))} </CTableDataCell>
      <CTableDataCell>
        <Link to="/admin/teams/projects">
          <CButton style={{
            color: "white",
            backgroundColor: "#2FB85D",
            border: "2px solid #2FB85D",
            margin: "2px",
            cursor: "pointer",
            boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.2)",
          }}> Show Projects
          </CButton>
        </Link>
        <CButton
          style={{
            color: "white",
            backgroundColor: "#3299FE",
            border: "2px solid #3299FE",
            margin: "2px",
            cursor: "pointer",
            boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.2)",
          }} onClick={() => setEdit(ele._id)}>
          Edit Admin
        </CButton>
        <CButton
          style={{
            color: "white",
            backgroundColor: "#F37C18",
            border: "2px solid #F37C18 ",
            margin: "2px",
            cursor: "pointer",
            boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.2)",
          }}
          onClick={() => {
            setDel(true);
            setConfirmDelname(`${ele._id}-${ele.email}-Suspend`);
          }}
        >
          Suspend
        </CButton>
        <CButton
          style={{
            color: "white",
            backgroundColor: "#E55353",
            border: "2px solid #E55353 ",
            margin: "2px",
            cursor: "pointer",
            boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.2)",
          }}
          onClick={() => {
            setDel(true);
            setConfirmDelname(`${ele._id}-${ele.email}-Delete`);
          }}
        >
          Delete Admin
        </CButton>
      </CTableDataCell>
    </CTableRow>
  );
}

export default ReadOnly;